:root {
	--max-width: 580px;
	--main-color: #41cd8c;
	--second-color: #45d0dd;
	--kinda-black: #2b2b2b;
	--gray: #646464;
}
@media (max-width: 780px) {
	:root {
		--max-width: 100vw;
	}
}
body {
	margin: 0;
	background-color: rgb(245, 245, 245);
	direction: rtl;
	font-family: "Cairo", sans-serif;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none;
	overscroll-behavior-y: contain;
}

/************** MainPage **************/

.MainPage {
	position: relative;
	margin: auto;
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	padding-bottom: 95px;
	text-align: center;
	opacity: 0;
	transition: opacity 500ms;
}
#header {
	position: sticky;
	z-index: 1;
}
#Gradient1 stop:nth-child(1) {
	stop-color: var(--main-color);
}
#Gradient1 stop:nth-child(2) {
	stop-color: var(--second-color);
}
#title {
	position: fixed;
	top: 25px;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 2;
	color: white;
	margin: 0;
	margin-top: -5px;
	font-size: calc(20px + 6 * ((100vw - 320px) / 680));
}
.remainValues {
	display: flex;
	justify-content: space-evenly;
	width: 100%;
	position: absolute;
	top: 37%;
	transform: translate(0, -50%);
}
.caloriesRemain,
.caloriesEaten {
	margin: auto;
	text-align: center;
	font-weight: bold;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	color: white;
	width: 100px;
}
.caloriesRemain p,
.caloriesEaten p {
	margin: 0px;
	margin-bottom: 0px;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
}
.caloriesRemain p:nth-child(1),
.caloriesEaten p:nth-child(1) {
	font-family: sans-serif;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
}
#macrosPercent {
	position: absolute;
	width: 100%;
	display: flex;
	justify-content: space-around;
	color: white;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
.macrosPercentContainer {
	position: relative;
	width: 29%;
	height: 5px;
	border-radius: 0.5px;
	background-color: rgb(250, 250, 245, 0.5);
}
.macrosPercentLine {
	position: absolute;
	right: 0px;
	top: 0px;
	height: 5px;
	width: 0;
	border-radius: 0.5px;
	background-image: linear-gradient(-225deg, #fffeff 0%, #d7fffe 100%);
	transition: width 500ms ease;
}
.macroName {
	position: absolute;
	top: -5px;
	left: 50%;
	transform: translate(-50%, -100%);
	margin: 0px;
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}
.percentNum {
	margin: 0;
	margin-top: 10px;
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}
.percentNum span {
	margin: 3px;
}
#addButton {
	position: sticky;
	margin-left: 15px;
	bottom: 90px;
	float: left;
	width: 55px;
	height: 55px;
	border-radius: 50%;
	z-index: 3;
	cursor: pointer;
	background-color: var(--main-color);
	box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
}
.circleButton {
	width: 60px;
	height: 60px;
	border-radius: 50%;
	z-index: 3;
	cursor: pointer;
}
.floatButton {
	position: fixed;
	bottom: 60px;
	transform: translate(-50%, -50%) scale(0);
	text-align: center;
	background-color: white;
	-webkit-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
	-moz-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
	box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
	display: none;
}
.floatButton p {
	position: absolute;
	left: 50%;
	top: 60px;
	transform: translate(-50%);
	margin: 0;
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}
.floatButton img {
	margin-top: 5px;
	width: 50px;
}

#addButton svg {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
#addFoodBG {
	position: fixed;
	top: 0;
	left: 0;
	margin: 0;
	width: 100vw;
	height: 100vh;
	z-index: 3;
	background-color: #f1f1e6;
	opacity: 0.95;
	display: none;
	opacity: 0;
	touch-action: none;
}
#dateSelector {
	display: flex;
	justify-content: space-between;
	align-items: center;
	max-width: var(--max-width);
	min-width: 320px;
}
#dateSelector p {
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
	color: var(--gray);
	direction: ltr;
	margin: 20px 0;
	transition: margin 500ms;
}
.dateArrow {
	margin: 10px;
	width: 20px;
	height: 20px;
	cursor: pointer;
	fill: var(--main-color);
}
#diaryContainer {
	white-space: nowrap;
	overflow-x: hidden;
	width: calc(100% - 14px);
	margin: auto;
	padding-right: 7px;
	padding-left: 7px;
	padding-bottom: 5px;
}
@media (max-width: 400px) {
	.floatButton img {
		margin-top: 8px;
		width: 45px;
	}
	.circleButton {
		width: 55px;
		height: 55px;
	}
	#addButton svg {
		width: 18px;
		height: 18px;
	}
	#addButton {
		width: 50px;
		height: 50px;
	}
	.dateArrow {
		width: 18px;
		height: 18px;
	}
}

/************** diary **************/

#pageBody {
	display: inline-block;
	vertical-align: top;
	width: 100%;
	max-width: var(--max-width);
	min-width: 320px;
}

#pageBody > div:first-child {
	margin: 0;
}
.mealPopUp {
	position: absolute;
	top: 42px;
	left: 20px;
	background-color: whitesmoke;
	border-radius: 5px;
	box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
	transform: scale(0);
	transform-origin: 0 0;
	transition: transform 200ms;
}
.mealPopUp p {
	padding: 10px 30px;
	margin: 0;
	color: var(--kinda-black);
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
	text-align: center;
	cursor: pointer;
}
.mealPopUp p:hover {
	color: var(--main-color);
}
.mealName {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: #ffffff;
	color: #1f1f1e;
	width: 100%;
	margin-top: 20px;
	vertical-align: middle;
	text-align: right;
	box-sizing: border-box;
	-webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	-moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.mealName h3 {
	margin: 0;
	padding: 8px 20px;
	font-weight: normal;
	color: var(--kinda-black);
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
}
.mealName svg {
	width: 18px;
	height: 18px;
	fill: #858585;
	margin-left: 2%;
	cursor: pointer;
}
.mealName svg:hover {
	fill: var(--main-color);
}
.mealFoods {
	width: 100%;
	color: #1f1f1e;
	box-sizing: border-box;
	background-color: #fafaf5;
	-webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	-moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.addYourFoodText {
	margin: 0;
	color: var(--kinda-black);
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
}
.mealFoods img {
	width: 50px;
}
.mealFoodsImgP {
	padding: 5px 0;
	cursor: pointer;
}
.mealKcalSum {
	background-color: white;
}
.mealKcalSum p {
	margin: 0;
	padding: 8px 20px;
	font-weight: normal;
	color: var(--gray);
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	text-align: right;
}

@media (max-width: 400px) {
	.mealFoods img {
		width: 45px;
	}
	.mealName svg {
		width: 18px;
		height: 18px;
	}
}

/************** NavicationBar **************/

#navicationBar {
	position: fixed;
	bottom: 0;
	left: 50%;
	transform: translate(-50%);
	margin: 0;
	height: 70px;
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	text-align: center;
	display: flex;
	justify-content: space-around;
	align-items: center;
	background-color: white;
	border-top-right-radius: 5px;
	border-top-left-radius: 5px;
	padding-top: 5px;
	z-index: 1;
	box-shadow: 0px -5px 10px -5px #00000040;
}
.navElements {
	cursor: pointer;
}
.navElements p {
	margin: 0;
	color: #7d7d7d;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	line-height: 15px;
	height: 0;
	transform-origin: bottom;
	transform: scale(0);
	transition: transform 250ms;
}
.navElements svg {
	width: 20px;
	height: 20px;
	fill: #7d7d7d;
}
.navElements:hover svg {
	fill: black;
}
.navElements:hover p {
	color: black;
}

@media (max-width: 450px) {
	.navElements svg {
		width: 18px;
		height: 18px;
	}
	#navicationBar {
		height: 55px;
	}
	#addButton {
		bottom: 70px;
	}
}

/************** SearchPage **************/

#SearchPage {
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	margin: auto;
	transition: bottom 250ms, opacity 250ms;
}
#createNewBG {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgb(241, 241, 230, 1);
	z-index: 1;
	display: none;
	opacity: 0;
	transition: opacity 350ms;
}
#createNewContainer {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 300px;
	background-color: white;
	border-radius: 5px;
	z-index: 2;
	box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
	display: none;
	opacity: 0;
	transition: opacity 350ms;
}
#createNewContainer p {
	text-align: center;
	color: var(--kinda-black);
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
	margin: 40px auto;
	cursor: pointer;
}
#createNewContainer p:hover {
	color: var(--main-color);
}
#searchPageHeader {
	position: sticky;
	top: 0;
	width: 100%;
	height: 150px;
	background-color: var(--main-color);
}
#searchPageHeaderTopContainer {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 90%;
	margin: auto;
	padding: 10px 0;
}
#searchPageBack {
	width: 35px;
	height: 35px;
	fill: white;
	cursor: pointer;
}
#searchPageBack:hover {
	fill: black;
}
#searchPageHeaderTitle {
	text-align: right;
	color: white;
	margin: 0;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
	line-height: 40px;
}
#searchBar {
	display: block;
	margin: auto;
	width: 90%;
	height: 45px;
	margin-top: 15px;
	padding-right: 50px;
	padding-left: 10px;
	border: none;
	border-radius: 10px;
	box-shadow: 0px 0px 10px 0px #00000040;
	background-image: url(/assets/search.png);
	background-repeat: no-repeat;
	background-size: 20px 20px;
	background-position: calc(100% - 20px) 50%;
	font-size: 18px;
	font-family: "Cairo", sans-serif;
	transition: top 500ms;
}
#searchBar::placeholder {
	font-size: 18px;
	font-family: "Cairo", sans-serif;
}
#searchOption {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	grid-template-rows: repeat(2, 1fr);
	grid-column-gap: 10px;
	grid-row-gap: 10px;
	margin: 10px;
	border-radius: 10px;
}
#searchOption p {
	display: grid;
	padding: 15%;
	justify-items: center;
	align-self: stretch;
	justify-self: stretch;
	margin: 0;
	cursor: pointer;
	font-weight: bold;
	background-color: white;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	color: var(--kinda-black);
	border-radius: 3px;
}
#searchOption p:hover {
	background-color: var(--main-color);
	color: white;
}
#searchOption svg {
	margin: 15px 0;
	fill: var(--main-color);
}
#searchOption p:hover svg {
	fill: white;
}
.searchResultsTitles {
	margin: 3px 20px;
	color: var(--kinda-black);
	font-size: 15px;
}
#searchNoResults {
	display: none;
	text-align: center;
	color: var(--kinda-black);
	margin-top: 50px;
}
@media (max-width: 450px) {
	#searchPageBack {
		width: 30px;
		height: 30px;
	}
}

/************** SearchItems **************/

.searchItemsCategoriesIcon {
	background-image: url(/assets/itemsCategoriesIcons/empty.png);
	background-size: contain;
	background-repeat: no-repeat;
	width: 40px;
	height: 40px;
	display: inline-block;
	margin-left: 20px;
	margin-right: 20px;
}
.searchItemsContainer {
	border-bottom: solid 2px rgb(245, 245, 245);
	cursor: pointer;
	padding-bottom: 10px;
	padding-top: 10px;
	background-color: #fafaf5;
	display: flex;
	align-items: center;
	text-align: right;
}
.searchItemsContainer:hover {
	background-color: var(--second-color);
	color: white;
}
.searchItemsContainer:hover p {
	color: white;
}

.searchItemsName {
	margin: 0;
	font-size: calc(13px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
	color: #1f1f1e;
}

.searchItemsCategories {
	margin: 0;
	font-size: calc(10px + 6 * ((100vw - 320px) / 680));
	color: #777775;
}
.searchItemsInfo {
	margin: 0;
	font-size: calc(11px + 6 * ((100vw - 320px) / 680));
	color: #1f1f1e;
}
.searchItemsInfo span {
	font-family: Arial, Helvetica, sans-serif;
	font-weight: bolder;
}
.searchItemsVerified {
	width: 18px;
	height: 18px;
	vertical-align: middle;
	margin-right: 5px;
}

/************** ItemViewer **************/

#itemViewerToast {
	position: fixed;
	bottom: 0px;
	left: 50%;
	transform: translate(-50%, 0);
	background-color: var(--main-color);
	padding: 10px 30px;
	width: fit-content;
	margin: 0;
	border-radius: 7px;
	opacity: 0;
	color: white;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	display: none;
	transition: all 350ms;
}
#itemViewerPage {
	width: 100%;
	max-width: var(--max-width);
	min-width: 320px;
	margin: auto;
	transition: bottom 250ms, opacity 250ms;
}
#itemViewerHeaderButtonsContainer {
	position: relative;
	width: 100%;
	height: 175px;
	background-color: var(--second-color);
}
#itemViewerHeader {
	position: sticky;
	top: 0;
	display: grid;
	grid-template-columns: repeat(2, 1fr) 0.4fr;
	grid-template-rows: auto;
	align-items: center;
	direction: ltr;
	padding: 20px;
	background-color: var(--second-color);
	z-index: 1;
}
#itemViewerHeaderTitle {
	color: white;
	margin: 0;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
	text-align: end;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}
#itemViewerBackButton {
	fill: white;
	width: 35px;
	height: 35px;
	cursor: pointer;
}
#itemViewerBackButton:hover {
	fill: black;
}
#itemViewerFavouriteButton {
	fill: white;
	width: 35px;
	height: 35px;
	justify-self: end;
	cursor: pointer;
}
#itemViewerTrashCan {
	fill: white;
	width: 35px;
	height: 35px;
	cursor: pointer;
}
#itemViewerTrashCan:hover {
	fill: black;
}
#itemViewerHeaderItemName {
	position: absolute;
	bottom: 20px;
	right: 20px;
	color: white;
	margin: 0;
	font-weight: bold;
	font-size: calc(25px + 6 * ((100vw - 320px) / 680));
	width: 75%;
}
#itemViewerOkButton {
	fill: white;
	width: 40px;
	height: 40px;
	cursor: pointer;
}
#itemViewerOkButtonBg {
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	bottom: -30px;
	left: 20px;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	background-color: var(--main-color);
}
#itemViewerInputsContainer {
	display: flex;
	align-items: center;
	justify-content: space-evenly;
	background-color: white;
	padding-top: 50px;
	padding-bottom: 25px;
}
#itemViewerSelectServing {
	width: 65%;
	height: 30px;
	color: var(--kinda-black);
	font-size: 18px;
	font-weight: bold;
	background-color: white;
	border: none;
	border-bottom: solid 1px var(--kinda-black);
}
.itemViewerSelectServingItems {
	color: var(--kinda-black);
}
#itemViewerInput {
	padding: 0;
	width: 15%;
	height: 30px;
	color: var(--kinda-black);
	font-size: 18px;
	font-weight: bold;
	text-align: center;
	border: none;
	border-bottom: solid 1px var(--kinda-black);
}
#itemViewerSelectMeal {
	width: 87%;
	height: 30px;
	margin: auto;
	margin-bottom: 25px;
	color: var(--kinda-black);
	font-size: 18px;
	font-weight: bold;
	background-color: white;
	border: none;
	border-bottom: solid 1px var(--kinda-black);
}
#itemViewerKcal {
	display: flex;
	align-items: baseline;
	color: var(--kinda-black);
	font-size: 30px;
	font-weight: bold;
	margin: 0;
	background-color: white;
	margin-bottom: 20px;
	padding: 0px 7% 25px 0;
}
#itemViewerKcal span {
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	font-weight: normal;
	padding-right: 10px;
}

#itemViewerNutritionInfoTitle {
	color: var(--kinda-black);
	margin: 0;
	padding: 20px 20px 0 0;
	background-color: white;
	font-weight: bold;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
}
#itemViewerNutritionInfoSubTitle {
	color: var(--kinda-black);
	margin: 0;
	padding-right: 20px;
	background-color: white;
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
}

#nutritionInfoPercentContainer {
	display: flex;
	justify-content: space-evenly;
	padding: 45px 0;
	background-color: white;
}
.percentTitles {
	text-align: center;
	margin: 0;
	color: var(--kinda-black);
}
#itemViewerNutritionInfoDetail {
	display: flex;
	justify-content: space-between;
	padding: 0 20px;
	padding-bottom: 20px;
	background-color: white;
}
.itemViewerNutritionInfoMain {
	font-weight: bold;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	color: var(--kinda-black);
}
.itemViewerNutritionInfoSub {
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
	margin-top: 2px;
	margin-bottom: 2px;
	color: var(--kinda-black);
}
#itemViewerNutritionInfoValues p {
	text-align: left;
}
@media (max-width: 450px) {
	#itemViewerBackButton {
		width: 30px;
		height: 30px;
	}
	#itemViewerFavouriteButton {
		width: 30px;
		height: 30px;
	}
	#itemViewerTrashCan {
		width: 30px;
		height: 30px;
	}
	#itemViewerOkButton {
		width: 30px;
		height: 30px;
	}
	#itemViewerOkButtonBg {
		width: 50px;
		height: 50px;
		bottom: -25px;
	}
}

/************** CategoriesList **************/

#categoriesListPage {
	width: 100%;
	min-width: 320px;
	max-width: var(--max-width);
	margin: auto;
	transition: bottom 300ms;
}
#categoriesListPageHeader {
	position: sticky;
	top: 0;
	z-index: 1;
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	height: 70px;
	background-color: var(--main-color);
}
#categoriesListPageBack {
	margin-left: 20px;
	width: 35px;
	height: 35px;
	fill: white;
	cursor: pointer;
}
#categoriesListPageBack:hover {
	fill: black;
}
#categoriesListPageHeaderTitle {
	color: white;
	margin: 0;
	margin-right: 30px;
	font-size: calc(18px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
#categoriesListBody {
	list-style: none;
	padding: 0;
	margin-top: 5px;
	width: 100%;
	max-width: var(--max-width);
	min-width: 320px;
}
#categoriesListBody li {
	position: relative;
	background-color: white;
	font-size: calc(17px + 6 * ((100vw - 320px) / 680));
	cursor: pointer;
	margin-bottom: 5px;
	padding: 10px;
	padding-right: 45px;
