	justify-content: space-between;
	margin: 0 13px;
}
#macrosContainer {
	width: 100%;
	max-width: 100%;
	overflow-x: hidden;
	margin: auto;
	background-color: white;
	color: white;
	background-color: lightgray;
	white-space: nowrap;
}
#macrosCarbPrecent {
	display: inline-block;
	height: 35px;
	text-align: center;
	background-color: #fb683e;
	transition: width 350ms;
}
#macrosProteinPrecent {
	display: inline-block;
	height: 35px;
	text-align: center;
	background-color: #8276c9;
	transition: width 350ms;
}
#macrosFatPrecent {
	display: inline-block;
	height: 35px;
	text-align: center;
	background-color: #33a9e9;
	transition: width 350ms;
}
#carbContainer {
	display: flex;
	align-items: baseline;
	justify-content: space-around;
	background-color: #fb683e;
	color: white;
	border-radius: 10px;
	margin-top: 30px;
	text-align: center;
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#proteinContainer {
	display: flex;
	align-items: baseline;
	justify-content: space-around;
	background-color: #8276c9;
	color: white;
	border-radius: 10px;
	margin-top: 30px;
	text-align: center;
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#fatContainer {
	display: flex;
	align-items: baseline;
	justify-content: space-around;
	background-color: #33a9e9;
	color: white;
	border-radius: 10px;
	margin-top: 30px;
	text-align: center;
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.macrosPercentTitles {
	margin: 15px 0;
	font-size: calc(16px + 6 * ((100vw - 320px) / 680));
}
.macrosPercentInputs {
	width: 100%;
	font-size: calc(16px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
	color: white;
	text-align: center;
	border: none;
	border-bottom: solid 1px white;
	background-color: rgb(0, 0, 0, 0);
}
.macrosPercentInputs::placeholder {
	color: white;
}
.macrosPercentKcal {
	font-size: calc(14px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
.macrosPercentSpan {
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	font-weight: normal;
}
.macrosPercentGram {
	font-size: calc(12px + 6 * ((100vw - 320px) / 680));
	font-weight: bold;
}
#autoAssign {
	display: block;
	margin: 35px auto;
	width: 100%;
	border: none;
	border-radius: 10px;
	padding: 14px 15px;
	font-weight: bold;
	font-size: calc(15px + 6 * ((100vw - 320px) / 680));
	font-family: "Cairo", sans-serif;
	color: white;
	background-color: var(--main-color);
	box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#auto:hover {
	background-color: var(--second-color);
}
